"use client"

import { useState, useEffect } from "react"
import { Search, Menu, Phone, MapPin, ChevronDown, ChevronRight, X, ShoppingCart, User } from "lucide-react"
import Link from "next/link"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Sheet, SheetContent, SheetTrigger, SheetClose } from "@/components/ui/sheet"
import { Collapsible, CollapsibleContent, CollapsibleTrigger } from "@/components/ui/collapsible"
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar"

export default function MobileHeader({ groups = [] }) {
    const [categoryOpen, setCategoryOpen] = useState(true)
    const [sheetOpen, setSheetOpen] = useState(false)

    // Prevent scrolling when mobile menu is open
    useEffect(() => {
        if (sheetOpen) {
            document.body.style.overflow = "hidden"
        } else {
            document.body.style.overflow = "auto"
        }
        return () => {
            document.body.style.overflow = "auto"
        }
    }, [sheetOpen])

    return (
        <header className="w-full">
            {/* Promo banner */}
            <div className="bg-red-600 text-white py-2 px-4 flex justify-between items-center">
                <span className="text-yellow-300 font-bold">Ưu đãi đặc biệt!</span>
                <Button variant="secondary" size="sm" className="rounded-full font-bold px-4 py-1 h-auto">
                    XEM NGAY
                </Button>
            </div>

            {/* Main header */}
            <div className="bg-red-600 text-white py-2 px-4 flex items-center justify-between">
                {/* Logo */}
                <div className="flex items-center">
                    <Avatar className="h-10 w-10 bg-white">
                        <AvatarImage src="/placeholder.svg" alt="Logo" />
                        <AvatarFallback className="text-xs text-gray-600">stana</AvatarFallback>
                    </Avatar>

                    <Sheet open={sheetOpen} onOpenChange={setSheetOpen}>
                        <SheetTrigger asChild>
                            <Button variant="ghost" size="icon" className="ml-2 text-white">
                                <Menu className="h-6 w-6" />
                            </Button>
                        </SheetTrigger>
                        <SheetContent side="left" className="p-0 w-[85%] max-w-sm" showCloseButton={false}>
                            {/* Menu header */}
                            <div className="bg-red-600 text-white p-4 flex justify-between items-center">
                                <div className="flex items-center">
                                    <Avatar className="h-10 w-10 bg-white mr-2">
                                        <AvatarImage src="/placeholder.svg" alt="Logo" />
                                        <AvatarFallback className="text-xs text-gray-600">stana</AvatarFallback>
                                    </Avatar>
                                    <span className="text-xl font-bold text-white">CarGear</span>
                                </div>
                                <SheetClose asChild>
                                    <Button variant="ghost" size="icon" className="text-white">
                                        <X className="h-6 w-6" />
                                    </Button>
                                </SheetClose>
                            </div>

                            {/* Menu content */}
                            <div className="flex-1 overflow-y-auto">
                                {/* Login button */}
                                <Link href="/login" className="block mx-4 my-4">
                                    <Button
                                        variant="outline"
                                        className="w-full border-red-500 text-red-600 rounded-full p-3 h-auto flex items-center justify-center"
                                    >
                                        <User className="h-5 w-5 mr-2" />
                                        <span>Đăng nhập / Đăng ký</span>
                                    </Button>
                                </Link>

                                {/* Location */}
                                <div className="flex items-center justify-between px-6 py-3 border-b">
                                    <div className="flex items-center">
                                        <MapPin className="h-5 w-5 text-red-600 mr-2" />
                                        <span className="text-gray-800">Hồ Chí Minh</span>
                                    </div>
                                    <ChevronRight className="h-5 w-5 text-gray-400" />
                                </div>

                                {/* Categories */}
                                <div className="border-b">
                                    <Collapsible open={categoryOpen} onOpenChange={setCategoryOpen}>
                                        <CollapsibleTrigger className="flex items-center justify-between w-full px-6 py-4">
                                            <span className="font-semibold text-gray-800">Danh mục sản phẩm</span>
                                            <ChevronDown
                                                className={`h-5 w-5 text-gray-600 transition-transform ${categoryOpen ? "transform rotate-180" : ""}`}
                                            />
                                        </CollapsibleTrigger>
                                        <CollapsibleContent className="pb-2">
                                            {groups && groups.length > 0 ? (
                                                groups.map((group, i) => (
                                                    <Link key={i} href="/#" className="flex items-center justify-between px-6 py-3">
                                                        <span className="text-gray-800">{group.name}</span>
                                                        <ChevronRight className="h-5 w-5 text-gray-400" />
                                                    </Link>
                                                ))
                                            ) : (
                                                <>
                                                    <Link href="/#" className="flex items-center justify-between px-6 py-3">
                                                        <span className="text-gray-800">Nội thất ô tô</span>
                                                        <ChevronRight className="h-5 w-5 text-gray-400" />
                                                    </Link>
                                                    <Link href="/#" className="flex items-center justify-between px-6 py-3">
                                                        <span className="text-gray-800">Ngoại thất ô tô</span>
                                                        <ChevronRight className="h-5 w-5 text-gray-400" />
                                                    </Link>
                                                    <Link href="/#" className="flex items-center justify-between px-6 py-3">
                                                        <span className="text-gray-800">Phụ kiện công nghệ</span>
                                                        <ChevronRight className="h-5 w-5 text-gray-400" />
                                                    </Link>
                                                </>
                                            )}
                                        </CollapsibleContent>
                                    </Collapsible>
                                </div>

                                {/* More options indicator */}
                                <div className="flex justify-center py-4">
                                    <div className="flex space-x-1">
                                        <div className="w-1 h-1 rounded-full bg-gray-400"></div>
                                        <div className="w-1 h-1 rounded-full bg-gray-400"></div>
                                        <div className="w-1 h-1 rounded-full bg-gray-400"></div>
                                    </div>
                                </div>
                            </div>
                        </SheetContent>
                    </Sheet>
                </div>

                {/* Search bar */}
                <div className="flex-1 mx-2">
                    <div className="relative">
                        <Input
                            type="text"
                            placeholder="Bạn tìm gì..."
                            className="w-full py-2 px-4 pr-10 rounded-full text-black text-sm h-auto"
                        />
                        <Button variant="ghost" size="icon" className="absolute right-0 top-1/2 transform -translate-y-1/2 h-full">
                            <Search className="h-5 w-5 text-gray-500" />
                        </Button>
                    </div>
                </div>

                {/* Action buttons */}
                <div className="flex items-center space-x-2">
                    <Button variant="ghost" size="icon" className="text-white">
                        <Phone className="h-5 w-5" />
                    </Button>
                    <Button variant="ghost" size="icon" className="text-white">
                        <ShoppingCart className="h-5 w-5" />
                    </Button>
                </div>
            </div>
        </header>
    )
}
